<template>
  <div>
    <el-table
      :data="tableData"
      empty-text="-">
      <el-table-column
        type="index"
        width="50">
      </el-table-column>
      <el-table-column
        label="描述">
        <template slot-scope="scope">
          <span>{{ scope.row.title}}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="状态">
        <template slot-scope="scope">
          <el-tag type="success" v-if="scope.row.status==1">进行中</el-tag>
          <el-tag type="info" v-if="scope.row.status==0">未开启</el-tag>
          <el-tag type="danger" v-if="scope.row.status==2">已结束</el-tag>
        </template>
      </el-table-column>
      <el-table-column
        label="时间">
        <template slot-scope="scope">
          <span>{{ scope.row.createdAt}}</span>
        </template>
      </el-table-column>
      <el-table-column label="操作">
      <template slot-scope="scope">
        <el-button
          size="mini"
          @click="handle(scope.row._id),dialogTableVisible = true" v-if="scope.row.status==1||scope.row.status==0">修改</el-button>
        <el-button  size="mini" type="success" v-if="scope.row.status==0" @click="start(scope.row._id)">开启</el-button>
        <el-button  size="mini" type="danger" v-if="scope.row.status==1" @click="end(scope.row._id)">结束</el-button>
      </template>
    </el-table-column>
    </el-table>
    <el-dialog title="修改" :visible.sync="dialogTableVisible" width="80%">
      <el-form ref="form" :model="formData" label-width="80px">
        <el-form-item label="描述">
          <el-input v-model="formData.title"></el-input>
        </el-form-item>
        <el-form-item label="内容">
          <editor v-model="formData.content" @getText="getText"></editor>
        </el-form-item>
        <el-form-item>
          <el-button type="success" @click="change">保存</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <pagination @pageSizeChange="pageSizeChange"  @currentPageChange="currentPageChange"></pagination>

  </div>
</template>

<script>
  import editor from '@/components/editor/index'
  import pagination from '@/components/pagination'

  export default {
    name: "list",
    components: {
      editor,
      pagination
    },
    data() {
      return {
        tableData:[],
        dialogTableVisible: false,
        formData:{
          title: '',
          content:'',
          contentText:''
        },
        id: "",//用来查询的id
        status1: 1,
        status2: 2,
        params:{
          pageSize: 5,
          page: 1
        }

      }
    },
    methods:{
      getText(val) {
        this.formData.contentText = val
      },
      getdata(){
        this.$axios.post('getDiscuss',this.params).then((res)=>{
          this.tableData = res.data
        })
      },
      handle(id){
        this.$axios.post('getDiscuss',{id}).then(res=>{
          this.formData = res.data[0]
          this.id = id
        })
      },
      change(){
        let params ={
          ...this.formData,
          id:this.id
        }
        this.$axios.post("updateDiscuss",params).then(res=>{
          if(res.code==200){
            this.$message({
              type: 'success',
              message: '修改成功!'
            });
            setTimeout(()=>{
              // this.$router.push('/crmaudits')
              this.$router.go(0)
            },1000)
          }

        })
      },
      start(id){
        let params ={
          status:this.status1,
          id:id
        }

        this.$axios.post("updateStatus",params).then(res=>{
          if(res.code==200){
            this.$message({
              type: 'success',
              message: '开启成功!'
            });
            setTimeout(()=>{
              // this.$router.push('/crmaudits')
              this.$router.go(0)
            },1000)
          }
        })
      },
      end(id){
        let params ={
          status:this.status2,
          id:id
        }

        this.$axios.post("updateStatus",params).then(res=>{
          if(res.code==200){
            this.$message({
              type: 'success',
              message: '结束成功!'
            });
            setTimeout(()=>{
              // this.$router.push('/crmaudits')
              this.$router.go(0)
            },1000)
          }
        })
      },
      pageSizeChange(val) {
        this.params.pageSize = val;
        this.getdata()
      },
      currentPageChange(val) {
        this.params.page = val;
        this.getdata()
      },
    },
    created(){
      this.getdata()
    }
  }
</script>

<style scoped>

</style>



